.countdown-timer {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 2rem 0;
  flex-wrap: wrap;
}

.countdown-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ff69b4;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  justify-content: center;
  box-shadow: 0 8px 25px rgba(255, 105, 180, 0.3);
  transition: transform 0.3s ease;
}

.countdown-item:hover {
  transform: scale(1.05);
}

.countdown-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: white;
  line-height: 1;
  margin-bottom: 0.2rem;
}

.countdown-label {
  font-size: 0.8rem;
  font-weight: 500;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .countdown-timer {
    gap: 1rem;
  }
  
  .countdown-item {
    width: 90px;
    height: 90px;
  }
  
  .countdown-number {
    font-size: 2rem;
  }
  
  .countdown-label {
    font-size: 0.7rem;
  }
}

@media (max-width: 480px) {
  .countdown-timer {
    gap: 0.5rem;
  }
  
  .countdown-item {
    width: 70px;
    height: 70px;
  }
  
  .countdown-number {
    font-size: 1.5rem;
  }
  
  .countdown-label {
    font-size: 0.6rem;
  }
} 