.hero-banner {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 2rem;
  position: relative;
}

.hero-content {
  max-width: 800px;
  z-index: 2;
}

.couple-names h1 {
  font-family: 'Great Vibes', cursive;
  font-size: 4.5rem;
  font-weight: 700;
  color: white;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.7);
  margin-bottom: 1rem;
  line-height: 1.2;
}

.wedding-message p {
  font-size: 1.5rem;
  font-weight: 300;
  color: white;
  margin-bottom: 3rem;
  letter-spacing: 2px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.cta-section {
  margin-top: 3rem;
}

.save-date-btn {
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid #ff69b4;
  color: #ff69b4;
  padding: 1rem 2.5rem;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 2px;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.save-date-btn:hover {
  background: #ff69b4;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 105, 180, 0.4);
}

.save-date-btn:active {
  transform: translateY(0);
}

@media (max-width: 768px) {
  .couple-names h1 {
    font-size: 3rem;
  }
  
  .wedding-message p {
    font-size: 1.2rem;
  }
  
  .save-date-btn {
    padding: 0.8rem 2rem;
    font-size: 0.9rem;
  }
} 